@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  padding: 30px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.InputSelect {
    margin-bottom: 20px;
    font-size: 14px;
    height: 35px;
    min-height: 45px; /*firefox*/
    width: 100%;
    padding: 12px;
    border-radius: 4px;
    box-sizing: border-box;
    resize: vertical;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#F2F2F2), to(#D4D4D4));
    background-image: -webkit-linear-gradient(top, #F2F2F2 0%, #D4D4D4 100%);
    background-image: linear-gradient(-180deg, #F2F2F2 0%, #D4D4D4 100%);
    border: 1px solid #979797;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,0.20), inset 0 2px 0 0 #FFFFFF;
}
.FormLabel{
    display: block;
    padding: 0 0 10px 0;
    font-size: 14px;
    font-weight: bold;
}
.FormBox{
    background: #FFFFFF;
    padding: 20px 25px;
    border-color: #979797;
    border: 1px solid #979797;
}
.InputText {
    font-size: 14px;
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    resize: vertical;
    background: #FFFFFF;
    border: 1px solid #979797;
    box-shadow: inset 0 1px 3px 0 rgba(0,0,0,0.50);
}


.buttonSearch {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#D8D8D8), to(#B3B3B3));
    background-image: -webkit-linear-gradient(top, #D8D8D8 0%, #B3B3B3 100%);
    background-image: linear-gradient(-180deg, #D8D8D8 0%, #B3B3B3 100%);
    border: 1px solid #979797;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,0.20), inset 0 2px 0 0 #FFFFFF;
    font-weight: bold;
    font-size: 14px;
    color: #4A4A4A;
    letter-spacing: 0;
    text-align: center;
    text-shadow: 0 1px 0 #FFFFFF; 
}
.buttonSearch:disabled{
    background: #FFFFFF;
    border: 1px solid #D8D8D8;
    color: #bbbbbb;
}

.searchBoxTitle{
    font-size: 16px;
    font-weight: bold;
}
